* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    height: 100%;
    width: 100%;
    overflow: hidden;
}

body.main {
    width: 100%;
    height: 100%;
    background: url(assets/img/bg_main.png) no-repeat center center;
    background-size: 100% 100%;
    position: relative;
}

main {

    width: 100%;
    height: 100%;
}

.all-time {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    display: block;
    width: 36.5%;
    height: 43%;
    background: url(assets/img/bg_all_time.svg) no-repeat center center;
    background-size: contain;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9;
    text-align: center;
}

.all-time span {
    color: #F8B62D;
    margin-top: 2%;
    margin-left: 3%;
    font-family: 'Palanquin', sans-serif;
    font-size: 7vw;
    text-shadow: 2px 2px 1px #000,
    -2px 2px 1px #000,
    2px -2px 1px #000,
    -2px -2px 1px #000,
    6px 6px 3px #000,
    0px 6px 3px #000,
    6px 0px 3px #000,
    0px 0px 3px #000;
}

main {
    display: flex;
    flex-wrap: wrap;
}

.player {
    height: 50%;
    width: 50%;
    position: relative;
    box-sizing: border-box;
}

.player-score {
    width: 88%;
    margin: auto;
    height: 14.5%;
    position: absolute;
    top: 12.5%;
    left: 0;
    right: 0;
    line-height: 0;

}

.player-score::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    display: block;
    background: url(assets/img/score-flame.png) no-repeat center center;
    background-size: 100% 100%;
    z-index: 2;
    line-height: 0;
}

.player-score .score-bar {
    position: relative;
    width: 100%;
    height: 100%;
    margin-left: auto;
    background: url(assets/img/bg_score.png) no-repeat center center;
    background-size: 100% 100%;
    border: 3px solid #BFC0C0;
}

.score-bar__elapsed {
    position: absolute;
    background: #000;
    height: 100%;
}

.player03 .player-score {
    bottom: 12.5%;
    top: auto;
}

.player04 .player-score {
    bottom: 12.5%;
    top: auto;
}

.player-name {
    position: absolute;
    top: 30%;
    left: 7%;
    z-index: 1;
    width: 60%;
    /*height: 20%;*/
}

.player-name img {
    object-fit: contain;
    object-position: left;
    width: 100%;
    /*height: 100%;*/
}

.player03 .player-name,
.player04 .player-name {
    position: absolute;
    top: auto;
    bottom: 30%;
    left: 7%;
}

.player-icon {
    width: 100%;
    height: 60%;
    position: absolute;
    top: 30%;
    left: 0%;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.player-icon img {
    width: auto;
    height: 100%;
    margin: auto;
}

.player03 .player-icon,
.player04 .player-icon {
    top: 10%;

}

.player-time {
    position: absolute;
    right: 7.2%;
    top: 28%;
    background: url(assets/img/bg_player-time01.svg) no-repeat right top;
    background-size: contain;
    width: 36%;
    height: calc(100% - 28%);
    text-align: center;
    padding-top: 4%;
    z-index: 2;
}

.player-time span {
    font-family: 'Palanquin', sans-serif;
    font-size: 3.7vw;
    width: 100%;
    text-align: center;
    line-height: 0;
    text-shadow: 2px 2px 1px #000,
    -2px 2px 1px #000,
    2px -2px 1px #000,
    -2px -2px 1px #000,
    4px 4px 2px #000,
    0px 4px 2px #000,
    4px 0px 2px #000,
    0px 0px 2px #000;
}

.player01 .player-time span {
    position: absolute;
    top: 4.8vw;
    right: 0;
    color: #E4007F;
}

.player02 .player-time {
    background-image: url(assets/img/bg_player-time02.svg);
    right: 7.2%;
    top: 28%;
}

.player02 .player-time span {
    position: absolute;
    top: 4.8vw;
    right: 0;
    color: #A40B5D;
}

.player03 .player-time {
    background: url(assets/img/bg_player-time03.svg) no-repeat right bottom;
    right: 7.2%;
    top: auto;
    bottom: 28%;
}

.player03 .player-time span {
    position: absolute;
    bottom: 3.8vw;
    right: 0;
    color: #009944;
}

.player04 .player-time {
    background: url(assets/img/bg_player-time04.svg) no-repeat right bottom;
    right: 7.2%;
    top: auto;
    bottom: 28%;
}

.player04 .player-time span {
    position: absolute;
    bottom: 3.8vw;
    right: 0;
    color: #FFE200;
}